@import url('https://fonts.googleapis.com/css?family=Montserrat:400,700,800,900');


html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
  -webkit-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  background-color: #111;
}

.mapwrap{
  position: relative;
  height: 75%;
}

.mapwrap:after{
  position: absolute;
  content:'';
  bottom:0;
  left:0;
  width:100%;
  height:70px;
  background-image: -webkit-gradient(linear, left top, left bottom, color-stop(10%, transparent), color-stop(50%, #111));
  background-image: -webkit-linear-gradient(transparent 10%, #111 50%);
  background-image: -o-linear-gradient(transparent 10%, #111 50%);
  background-image: linear-gradient(transparent 10%, #111 50%);
}
.map-holder {
  height: 100%;
}

