h1 {
  font-family: 'Montserrat', sans-serif;
  text-align: center;
  /* text-shadow: 0 0 5px rgba(255, 255, 255, 0.5); */
  position: absolute;
  bottom: 50%;
  margin-bottom: 200px;
  left: 0;
  right: 0;
  font-weight: inherit;
  text-shadow: 0 0 5px black;
}

.place-container {
  position: absolute;
  bottom: 13%;
  margin-top: -190px;
  height: 280px;
  left: 0;
  right: 0;
}
.place-container .swiper-container {
  height: 100%;
  width: 100%;
  position: relative;
  padding-bottom: 80px;
}
.place-container .swiper-wrapper {
  padding-bottom: 60px;
  padding-top: 30px;
}

.swiper-slide {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: center;
  align-items: center;
  width: 280px;
}
.swiper-slide .place-box {
  font-family: 'Montserrat', sans-serif;
  height: 100%;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  -webkit-box-shadow: 0px 20px 50px -15px black;
          box-shadow: 0px 20px 50px -15px black;
  border-radius: 6px;
  position: relative;

  cursor: pointer;
  -webkit-transition: .25s !important;
  -o-transition: .25s !important;
  transition: .25s !important;
}

.swiper-slide .place-box:hover{
  -webkit-box-shadow: 10px 20px 50px -15px black;
          box-shadow: 10px 20px 50px -15px black;
  -webkit-transform:translateY(-10px) scale(1.05) !important;
      -ms-transform:translateY(-10px) scale(1.05) !important;
          transform:translateY(-10px) scale(1.05) !important;
}

.swiper-slide{background-size: cover; background-repeat: no-repeat;}
.swiper-slide:nth-child(1){background-image: url(https://c1.staticflickr.com/8/7281/15982366453_66604f5141_k.jpg);}
.swiper-slide:nth-child(2){background-image: url(https://c1.staticflickr.com/1/552/31564232483_f458318e3e_h.jpg);}
.swiper-slide:nth-child(3){background-image: url(https://c1.staticflickr.com/1/732/21087390328_8e67e6f2be_h.jpg);}
.swiper-slide:nth-child(4){background-image: url(https://c1.staticflickr.com/5/4173/33661321473_334378af82_h.jpg);}
.swiper-slide:nth-child(5){background-image: url(https://c1.staticflickr.com/9/8080/8318354097_3e3c8de7cd_c.jpg);}
.swiper-slide:nth-child(6){background-image: url(https://c1.staticflickr.com/8/7266/7001421614_26e0971e3b_k.jpg);}
.swiper-slide:nth-child(7){background-image: url(https://c1.staticflickr.com/6/5462/9428588404_b688706388_h.jpg);}
.swiper-slide:nth-child(8){background-image: url(https://c1.staticflickr.com/8/7281/15982366453_66604f5141_k.jpg);}

.swiper-pagination {
  color:rgba(255,255,255,.4);
  font-size: 20px;
  bottom:0;
}
h2 {
  font-family: 'Montserrat', sans-serif;
  text-align: center;
  font-weight: 900;
  font-size:50px;
  color: #777;
  opacity: .8;
  max-width:90%;
  margin: 30px auto;
  mix-blend-mode: color-dodge;
  /* mix-blend-mode: color-burn; */

}

.place-footer {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}
.place-footer p {
  margin: 0;
  text-align: center;
  margin-bottom: .75em;
}



